body {
  margin: 0;
  padding: 0;
  border: 0;
  font-family: 'san_francisco_textregular';
  color: #FFFFFF;
  font-size: 18px;
  background-color: #1d2028;
  display: block;
  width: 100%;
  height: auto;
  min-height: 100vh;
  overflow-x: hidden;
}

a {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

.page-appear {
  opacity: 0.01;
  -webkit-transform: translateY(30px);
      -ms-transform: translateY(30px);
          transform: translateY(30px);
}

.page-appear.page-appear-active {
  -webkit-transition: opacity .3s ease-in , -webkit-transform .2s linear;
  transition: opacity .3s ease-in , -webkit-transform .2s linear;
  -o-transition: opacity .3s ease-in , transform .2s linear;
  transition: opacity .3s ease-in , transform .2s linear;
  transition: opacity .3s ease-in , transform .2s linear , -webkit-transform .2s linear;
  opacity: 1;
  -webkit-transform: translateY(0);
      -ms-transform: translateY(0);
          transform: translateY(0);
}

